.title {
    margin: 0 auto;
    text-align: center;
}

  * {
    margin: 0;
    padding: 0;
}

body {
    background: yellowgreen;
}

.box {
    /* display: relative; */
    /* background-size: 1280 960; */
    cursor:url('../img/cursor.bmp'), default;
    width: 1280px;
    height: 960px;
    background: url(../img/background.png) no-repeat;
    background-size: contain;
    max-width: 100%;
    max-height: 100%;
    margin: 0 auto;
    position: relative;
    /* z-index: 0; */
}

.box div {
    position: absolute;
}

.time_label {
    color: white;
    font-size: 30px;
    font-weight: bold;
    top: 7%;
    left: 75%;
}
.time_img {
    background: url(../img/timer.png) no-repeat;
    width: 240px;
    height: 100%;
    top: 5%;
    left: 81%;
    position: relative;
}
.time {
    width: 202px;
    height: 36px;
    background: blue;
    top: 7.2%;
    left: 82.5%;
}

.score_label {
    color: white;
    font-size: 30px;
    font-weight: bold;
    top: 13%;
    left: 75%;
}

.score {
    color: white;
    font-size: 30px;
    font-weight: bold;
    top: 13.3%;
    left: 85%;
}

.pause {
    width: 257px;
    height: 115px;
    background: url(../img/pause.png);
    top: 75%;
    left: 15%;
}

.start {
    width: 257px;
    height: 115px;
    background: url(../img/restart.png) no-repeat;
    background-size: cover;
    top: 75%;
    left: 15%;
    /* position: relative; */
}

.gameover, .restart, .pause {
    display: none;
}
